<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-card>
          <div slot="header">部门管理</div>
          <div class="header">
            <span>按关键词搜索&nbsp;&nbsp;</span>
            <div class="keyWord">
              <el-input placeholder="请输入关键词" v-model="departmentKey"></el-input>
            </div>
            <el-button @click="getDepartmentByKey" type="primary">搜索</el-button>
            <el-button @click="addDialog = true" style="margin-left: 20px;" type="primary">添加部门</el-button>

            <!-- 添加部门的dialog -->
            <el-dialog :modal-append-to-body="false" :visible.sync="addDialog" title="新增部门">
              <el-form :model="newForm" :rules="rules" ref="newForm">
                <el-form-item label="部门名称" prop="name">
                  <el-input autocomplete="off" v-model="newForm.name"></el-input>
                </el-form-item>

                <el-form-item label="父级部门" prop="pid">
                  <!-- <el-input v-model="form.name" autocomplete="off"></el-input> -->
                  <el-select filterable placeholder="请选择" v-model="newForm.pid">
                    <el-option
                      :key="d.value"
                      :label="d.label"
                      :value="d.value"
                      v-for="d in departments"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="负责人" prop="managerId">
                  <el-select filterable placeholder="请选择" v-model="newForm.managerId">
                    <el-option
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                      v-for="item in managers"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="打卡周期" prop="clockWeek">
                  <el-input autocomplete="off" v-model="newForm.clockWeek"></el-input>
                </el-form-item>
                <el-form-item label="状态" prop="state">
                  <el-radio :label="1" v-model="newForm.state">正常</el-radio>
                  <el-radio :label="2" v-model="newForm.state">禁用</el-radio>
                </el-form-item>
                <el-form-item label="连续打卡天数" prop="clockNumber">
                  <el-popover
                    content="连续打卡多少天不用传照片"
                    placement="top-start"
                    title="提示"
                    trigger="click"
                    width="250"
                  >
                    <el-input autocomplete="off" slot="reference" v-model="newForm.clockNumber"></el-input>
                  </el-popover>
                </el-form-item>
                <el-form-item label="上班" prop="workOn">
                  <el-time-select
                    :picker-options="{
                                                  start: '08:00',
                                                  step: '00:15',
                                                  end: '18:30'
                                                }"
                    placeholder="选择时间"
                    v-model="newForm.workOn"
                  ></el-time-select>
                </el-form-item>
                <el-form-item label="中午下班" prop="noonOff">
                  <el-time-select
                    :picker-options="{
                                                    start: '08:00',
                                                    step: '00:15',
                                                    end: '18:30'
                                                  }"
                    placeholder="选择时间"
                    v-model="newForm.noonOff"
                  ></el-time-select>
                </el-form-item>
                <el-form-item label="中午上班" prop="afternoonOn">
                  <el-time-select
                    :picker-options="{
                                                    start: '08:00',
                                                    step: '00:15',
                                                    end: '18:30'
                                                  }"
                    placeholder="选择时间"
                    v-model="newForm.afternoonOn"
                  ></el-time-select>
                </el-form-item>
                <el-form-item label="下班" prop="workOff">
                  <el-time-select
                    :picker-options="{
                                                    start: '08:00',
                                                    step: '00:15',
                                                    end: '18:30'
                                                  }"
                    placeholder="选择时间"
                    v-model="newForm.workOff"
                  ></el-time-select>
                </el-form-item>
              </el-form>
              <div class="dialog-footer" slot="footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button @click="addDepartment('newForm')" type="primary">确 定</el-button>
              </div>
            </el-dialog>
            <!-- 添加部门dialog结束 -->
          </div>
          <el-table :data="tableData" border height="700px" style="width: 100%">
            <el-table-column fixed label="部门名" prop="name"></el-table-column>
            <el-table-column :formatter="fmtParentDepartment" label="父级部门">
              <!-- prop="pid" -->
            </el-table-column>
            <el-table-column :formatter="fmtManager" label="负责人"></el-table-column>
            <el-table-column label="打卡周期" prop="clockWeek"></el-table-column>
            <el-table-column label="状态">
              <template slot-scope="scope">
                <el-tag
                  :type="scope.row.state === 1 ? 'success' : 'danger'"
                  disable-transitions
                >{{getState(scope.row.state)}}</el-tag>
              </template>
              <!-- <template slot-scope="scope">{{ getState(scope.row.state) }}</template> -->
            </el-table-column>
            <el-table-column fixed="right" label="操作">
              <template slot-scope="scope">
                <el-button @click="showUpdateDialog(scope.row)" size="small" type="primary">编辑</el-button>
                <el-button
                  @click="deleteDepartment(scope.row)"
                  size="small"
                  type="danger"
                  v-show="scope.row.state===2"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- 修改部门信息dialog -->
          <el-dialog
            :modal-append-to-body="false"
            :visible.sync="dialogVisible"
            title="修改部门信息"
            width="50%"
          >
            <el-form :model="form" :rules="rules" ref="form">
              <el-form-item label="部门名称" prop="name">
                <el-input autocomplete="off" v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="父级部门" prop="pid">
                <!-- <el-input v-model="form.name" autocomplete="off"></el-input> -->
                <el-select filterable placeholder="请选择" v-model="form.pid">
                  <el-option
                    :key="dd.value"
                    :label="dd.label"
                    :value="dd.value"
                    v-for="dd in departments"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="负责人" prop="managerId">
                <el-select filterable placeholder="请选择" v-model="form.managerId">
                  <el-option
                    :key="it.value"
                    :label="it.label"
                    :value="it.value"
                    v-for="it in managers"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="打卡周期" prop="clockWeek">
                <el-input autocomplete="off" v-model="form.clockWeek"></el-input>
              </el-form-item>
              <el-form-item label="状态" prop="state">
                <el-radio :label="1" v-model="form.state">正常</el-radio>
                <el-radio :label="2" v-model="form.state">禁用</el-radio>
              </el-form-item>
              <el-form-item label="连续打卡天数" prop="clockNumber">
                <el-popover
                  content="连续打卡多少天不用传照片"
                  placement="top-start"
                  title="提示"
                  trigger="click"
                  width="250"
                >
                  <el-input autocomplete="off" slot="reference" v-model="form.clockNumber"></el-input>
                </el-popover>
              </el-form-item>
              <el-form-item label="上班" prop="workOn">
                <el-time-select
                  :picker-options="{
                                  start: '07:00',
                                  step: '00:15',
                                  end: '18:30'
                                }"
                  placeholder="选择时间"
                  v-model="form.workOn"
                ></el-time-select>
              </el-form-item>
              <el-form-item label="中午下班" prop="noonOff">
                <el-time-select
                  :picker-options="{
                                    start: '07:00',
                                    step: '00:15',
                                    end: '18:30'
                                  }"
                  placeholder="选择时间"
                  v-model="form.noonOff"
                ></el-time-select>
              </el-form-item>
              <el-form-item label="中午上班" prop="afternoonOn">
                <el-time-select
                  :picker-options="{
                                    start: '07:00',
                                    step: '00:15',
                                    end: '18:30'
                                  }"
                  placeholder="选择时间"
                  v-model="form.afternoonOn"
                ></el-time-select>
              </el-form-item>
              <el-form-item label="下班" prop="workOff">
                <el-time-select
                  :picker-options="{
                                    start: '07:00',
                                    step: '00:15',
                                    end: '18:30'
                                  }"
                  placeholder="选择时间"
                  v-model="form.workOff"
                ></el-time-select>
              </el-form-item>
            </el-form>
            <span class="dialog-footer" slot="footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button @click="updateInfo('form')" type="primary">确 定</el-button>
            </span>
          </el-dialog>
          <!-- 修改部门信息结束 -->
          <!-- 分页插件 -->
          <div class="page">
            <el-pagination
              :current-page="currentpage"
              :total="total"
              @current-change="changedPage"
              background
              layout="prev, pager, next"
            ></el-pagination>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      departmentKey: '', // 部门检索的关键词
      tableData: [],
      pagesize: 10,
      currentpage: 1,
      total: null,
      url: '',
      departments: '',
      managers: null,
      departmentId: '',
      addDialog: false,
      newForm: {
        name: '',
        pid: '',
        managerId: '',
        workOn: '',
        noonOff: '',
        afternoonOn: '',
        workOff: '',
        clockWeek: '',
        clockNumber: '',
        state: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入部门名称', trigger: 'blur' }
        ],
        pid: [
          { required: true, message: '请选择上级部门', trigger: 'blur' }
        ],
        managerId: [
          { required: true, message: '请输入部门负责人', trigger: 'blur' }
        ],
        workOn: [
          { required: true, message: '请输入上班时间', trigger: 'blur' }
        ],
        noonOff: [
          { required: true, message: '请输入中午下班时间', trigger: 'blur' }
        ],
        afternoonOn: [
          { required: true, message: '请输入中午上班时间', trigger: 'blur' }
        ],
        workOff: [
          { required: true, message: '请输入下班下午时间', trigger: 'blur' }
        ],
        clockWeek: [
          { required: true, message: '请输入上班周期', trigger: 'blur' }
        ],
        clockNumber: [
          { required: true, message: '请输入连续打卡日期', trigger: 'blur' }
        ],
        state: [
          { required: true, message: '请输入状态', trigger: 'blur' }
        ]
      },
      dialogVisible: false,
      form: {
        name: '',
        pid: '',
        managerId: '',
        workOn: '',
        noonOff: '',
        afternoonOn: '',
        workOff: '',
        clockWeek: '',
        clockNumber: '',
        state: ''
      }

    }
  },
  created () {
    this.getDepartments();
    this.getManagers();
    this.getTableData();
  },
  methods: {
    // 删除部门
    deleteDepartment (row) {
      let _this = this;
      this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRequest('/department/del/' + row.id).then(res => {
          _this.getTableData();
          _this.$message({
            showClose: true,
            message: ' 删除成功',
            type: 'success'
          })
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    updateInfo (form) {
      var _this = this;
      this.$refs[form].validate((valid) => {
        if (valid) {
          _this.form.workOn = _this.computeStr2Time(_this.form.workOn);
          _this.form.noonOff = _this.computeStr2Time(_this.form.noonOff);
          _this.form.afternoonOn = _this.computeStr2Time(_this.form.afternoonOn);
          _this.form.workOff = _this.computeStr2Time(_this.form.workOff);
          this.postRequest(_this.url + '/department/update', _this.form).then(res => {
            if (res.data.code === 0) {
              _this.$message({
                showClose: true,
                message: ' 更新成功',
                type: 'success'
              });
              _this.dialogVisible = false;
              _this.newForm = {};
              _this.getTableData();
            }
          })
        }
      })
    },
    // 从前端传给后端时,要把时间字符串转换成秒数
    computeStr2Time (str) {
      var temparr = str.split(':');
      console.log(temparr);
      return temparr[0] * 3600 + temparr[1] * 60;
    },

    // 计算时间的方法,后台传过来的是秒数
    computeTime2str (num) {
      var min = num % 3600 / 60;
      var hour = Math.floor(num / 3600);
      if (hour < 10) {
        hour = '0' + hour;
      }
      if (min < 10) {
        min = '0' + min;
      }
      return hour + ':' + min;
    },
    // 显示修改部门信息的dialog
    showUpdateDialog (row) {
      this.dialogVisible = true;
      this.form = row;
      // this.form.managerId = row.managerId;
      // this.form.pid = row.pid;
      this.form.workOn = this.computeTime2str(row.workOn);
      this.form.workOff = this.computeTime2str(row.workOff);
      this.form.afternoonOn = this.computeTime2str(row.afternoonOn);
      this.form.noonOff = this.computeTime2str(row.noonOff);
    },
    // 添加部门
    addDepartment (newForm) {
      this.$refs[newForm].validate((valid) => {
        if (valid) {
          var _this = this;
          this.postRequest(_this.url + '/department/add', _this.newForm).then(res => {
            if (res.data.code === 0) {
              _this.$message({
                showClose: true,
                message: ' 添加成功',
                type: 'success'
              });
              _this.addDialog = false;
              _this.newForm = {};
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: ' 添加失败,请重试',
                type: 'error'
              });
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 格式化部门负责任人
    fmtManager (row) {
      if (this.managers === 0 || this.managers == null) {
        return;
      }
      let data = this.managers.filter(function (item) {
        if (item.value === row.managerId) {
          return item.label;
        }
      });
      if (data instanceof Array) {
        return data[0].label;
      }
      return '暂无';
    },
    // 格式化父级部门
    fmtParentDepartment (row) {
      if (this.departments.length === 0 || row.pid === 0 || this.departments == null) {
        return;
      }
      var data = this.departments.filter((item) => {
        if (row.pid === item.value) {
          return item;
        }
      })
      return data[0].label;
    },
    getDepartmentByKey () {
      var _this = this;
      this.getRequest('/department/info?keyWord=' + _this.departmentKey).then(res => {
        _this.currentpage = res.data.currentpage;
        _this.tableData = res.data.records
        _this.total = res.data.total;
      })
    },
    // 获取负责人
    getManagers: function () {
      var _this = this;
      this.getRequest(_this.url + '/employeeInfo').then((response) => {
        _this.managers = response.data;
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '获取负责人列表失败',
          type: 'error'
        });
      })
    },
    // 部门的select下拉选择框
    getDepartments: function () {
      console.log('get bumen ');
      var _this = this;
      this.getRequest(_this.url + '/department/realAll').then(res => {
        this.departments = res.data;
      }).catch(function () {
        _this.$message({
          showClose: true,
          message: '获取部门列表失败',
          type: 'error'
        });
      })
    },
    // 格式化部门状态
    getState (val) {
      return val === 1 ? '正常' : '禁用';
    },
    getTableData () {
      var _this = this;
      this.getRequest(_this.url + '/department/info?pageNum=' + _this.currentpage + '&pageSize=' + _this.pagesize).then(res => {
        _this.currentpage = res.data.current;
        _this.tableData = res.data.records
        _this.total = res.data.total;
      })
    },
    changedPage (currentPage) {
      var _this = this;
      var url = _this.url + '/department/info?pageNum=' + currentPage;
      if (this.departmentId) {
        url += '&did=' + _this.departmentId;
      }
      this.getRequest(url).then(res => {
        _this.currentpage = currentPage
        _this.tableData = res.data.records
        _this.total = res.data.total;
      });
    }
  }
}
</script>

<style  scoped>
.header {
  margin-bottom: 6px;
}
.header .keyWord {
  display: inline-block;
}
.page {
  text-align: center;
}
</style>


